@mixin blackCheckBox {
    [type="checkbox"]:not(:checked),
    [type="checkbox"]:checked{
        position: absolute;
        left:-9999px;
    }
    [type="checkbox"]:not(:checked)+label,
    [type="checkbox"]:checked+label{
        // cursor: pointer;
        position: relative;
        padding-left: 25px;
        cursor: pointer;
    }
    [type="checkbox"]:not(:checked)+label:before,
    [type="checkbox"]:checked+label:before{
        content: '';
        position: absolute;
        left: 0;
        top: -12px;
        width: 15px;
        height: 15px;
        background: #fff;
        border-radius: 3px;
        box-shadow: inset 0 3px 5px rgba(0, 0, 0, .1);
        border:1px solid rgba(0, 0, 0, .4);
        // border-radius: 50%;
        // background-color: $borderColor_3;
        outline: none;
    }
    [type="checkbox"]:not(:checked)+label:after,
    [type="checkbox"]:checked+label:after {
    content: '';
    position: absolute;
    top: -7px;
    left: 2.5px;
    font-size: 12px;
    font-weight: bold;
    line-height: 0.4;
    color: #333333;
    transition: all .2s;
    }
    [type="checkbox"]:not(:checked)+label:after {
        opacity: 0;
        transform: scale(0);
      }
      
      [type="checkbox"]:checked+label:after {
        opacity: 1;
        transform: scale(1);
      }
}


