// Credit Queue
@mixin CQ_tabs_container($bgColor,$shadow,$pad,$h){
    
    background-color: $bgColor;
    height: $h;
    box-shadow: $shadow;
    display: flex;
    align-items: center;
    padding:$pad;
    position:relative;
}
@mixin CQ_tabs($bgColor,$txtColor,$borderColor,$fontsize,$pad,$height) {
    height: $height;
    width: fit-content;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background-color: $bgColor;
    border:1px solid $borderColor;
    color:$txtColor;
    padding: $pad;
    font-size: $fontsize;
    font-weight: bold;
    transition: all 0.17s ease-in-out;
    cursor: pointer;
}

//**********************Admin Vertical Bar***********************
@mixin menuImageIcon{
    width: 0.9rem;
    filter:none;
}
@mixin menuImageIcon2{
    filter:invert(26%) sepia(16%) saturate(5490%) hue-rotate(161deg) brightness(91%) contrast(98%);
    transition-delay:filter 0.3s ease-in-out;
}
@mixin menuBox($d,$mt,$mb,$al) {
    display: $d;
    margin-top: $mt;
    margin-bottom: $mb;
    align-items: $al;
}

@mixin scrollable{
    -ms-overflow-style: none; /* for Internet Explorer, Edge */
    scrollbar-width: none;
    overflow-y: scroll;
}
@mixin hoverTitleEffect{
    color:#03596E;
    font-weight: bold;
    background-color: #fff;
    padding:0.6rem 0;
    cursor: pointer;
    transition:all 0.1s ease-in-out;
}


